.App{
  text-align: center;
  display: flex;
  flex-direction: column;
  row-gap: 1cm;
}
.addTodoForm{
  display: flex;
  justify-content: center;
  column-gap: .2cm;
}
.addTodoInput{
  width: 8cm;
  height: 1cm;
  font-size: 25px;
  border: none;
  background-color: #D6F1FF;
  border-radius: 10px;
}
.addTodoInput:hover{
  border: 1px solid black;
}

.addTodoButton{
  width: 2cm;
  height: 1.1cm;
  background-color: blueviolet;
  color: whitesmoke;
  font-size: 25px;
  font-weight: bold;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}
.todoList{
  display: flex;
  column-gap: .3cm;
  justify-content: center;
  align-items: center;
  
}
.delTodo{
  width: .7cm;
  height: .7cm;
  cursor: pointer;
}
.sign-out{
  width: 2.5cm;
  height: .8cm;
  font-size: 16px;
  background-color: #28282b;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.sign-in{
  width: 5cm;
  height: .8cm;
  font-size: 16px;
  background-color: #28282b;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.userPhoto{
  width: 1.5cm;
  height: 1.5cm;
  border-radius: 50px;
}
.userPhotoSection{
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: .2cm;
}
